<template>
  <div class="roleContent">
    <el-dialog
      title="添加角色"
      :visible="dialogVisible"
      width="600px"
      :before-close="close"
    >
      <el-form>
        <el-form-item label="角色名称">
          <el-input style="width: 300px" v-model="form.name" />
        </el-form-item>
        <el-form-item label="角色权限">
          <div style="margin-top: 10px">
            <el-tree
              style="margin-left: 80px"
              ref="tree"
              :data="permisssionMenu"
              show-checkbox
              node-key="id"
              :props="defaultProps"
            />
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="修改角色"
      :visible="modifyStatus"
      width="600px"
      :before-close="close"
    >
      <el-form>
        <el-form-item label="角色名称">
          <el-input style="width: 300px" v-model="form.roleName" />
        </el-form-item>
        <el-form-item label="角色权限">
          <div style="margin-top: 10px">
            <el-tree
              style="margin-left: 80px"
              ref="tree"
              @check-change="onChange"
              :data="permisssionMenu"
              show-checkbox
              node-key="id"
              :props="defaultProps"
            />
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="confirmModify">确 定</el-button>
      </span>
    </el-dialog>
    <div class="addRole" @click="addRole">
      添加角色
    </div>
    <div class="sort-content">
      <el-table
        :data="userData.list"
        border
        style="width=100%"
        :header-cell-style="{ 'text-align': 'center', color: 'black' }"
        :cell-style="{ 'text-align': 'center' }"
      >
        <el-table-column prop="id" label="序号" />
        <el-table-column prop="name" label="角色账户名称" />
        <el-table-column label="状态" prop="desStatus" />
        <el-table-column prop="created_at" label="创建时间" />
        <el-table-column width="250px">
          <template slot="header" :slot-scope="scope">
            操作
          </template>
          <template slot-scope="scope">
            <el-button size="mini" @click="modify(scope.row.id)">
              修改
            </el-button>
            <el-switch
              style="margin-left: 30px"
              v-model="scope.row.status"
              active-text="启用"
              inactive-text="禁用"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="userData.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click="next"
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      scope: "scope",
      dialogVisible: false,
      modifyStatus: false,
      userData: [],
      limit: 10,
      pages: 1,
      defaultProps: {
        children: "_child",
        label: "name",
      },
      //  权限菜单
      permisssionMenu: [],
      // 角色权限列表
      permissionList: [],
      form: {},
      // 角色id
      roleId: "",
    };
  },
  created() {
    this.getUserList();
  },
  mounted() {
    this.getPermissionMenu();
  },

  methods: {
    // 获取角色列表
    async getUserList() {
      try {
        let res = await this.axios.get("/admin/getRoleList", {
          params: {
            limit: this.limit,
            pages: this.pages,
          },
        });
        this.userData = Object.assign(res.data, {
          list: res.data.list.map((item) => {
            return Object.assign(item, {
              status: item.status === 1 ? true : false,
              desStatus: item.status === 1 ? "启用" : "禁用",
            });
          }),
        });
      } catch (error) {
        console.log(error);
      }
    },
    addRole() {
      this.dialogVisible = true;
    },
    // 上一页
    prev() {
      this.pages--;
      this.getUserList();
    },
    // 获取权限菜单
    async getPermissionMenu() {
      try {
        let res = await this.axios.get("/admin/menu", {
          params: {
            // 获取节点
            type: 2,
          },
        });
        this.permisssionMenu = res.data;
      } catch (error) {
        console.log(error);
      }
    },
    // 添加角色
    async confirm() {
      let selectedNodes = this.$refs.tree
        .getCheckedKeys()
        .concat(this.$refs.tree.getHalfCheckedKeys());
      try {
        let res = await this.axios.post("/admin/addrole", {
          name: this.form.name,
          path: selectedNodes.join(","),
        });
        this.close();
        this.$message.success({
          message: res.msg,
        });
        this.pages = 1;
        this.getUserList();
      } catch (error) {
        console.log(error);
      }
    },
    // 修改
    modify(id) {
      this.getRolePermission(id);
      this.roleId = id;
      this.modifyStatus = true;
    },
    // 获取角色权限
    async getRolePermission(id) {
      try {
        let res = await this.axios.get("/admin/getrole", {
          params: {
            "role_id": id,
          },
        });
        this.$nextTick(() => {
          // 数据回显,解决数据半选问题
          res.data.path.forEach((item) => {
            let node = this.$refs.tree.getNode(item);
            if (node.isLeaf) {
              this.$refs.tree.setChecked(node, true);
            }else{
               node.indeterminate = true;
               node.checked = true;
            }
          });
        });
      } catch (error) {
        console.log(error);
      }
    },
    onChange(a) {
      const anode = this.$refs.tree.getNode(a);
      !anode.checked ? this.findchildren(anode.parent) : "";
    },
    findchildren(current) {
      const fnode = this.$refs.tree.getNode(current);
      if (!fnode.isLeaf) {
        fnode.indeterminate = true;
        fnode.checked = true;
      }
    },
    // 修改角色权限
    async confirmModify() {
      let selectedNodes = this.$refs.tree
        .getCheckedKeys()
        .concat(this.$refs.tree.getHalfCheckedKeys());
      try {
        let res = await this.axios.post("/admin/uprole", {
          "role_id": this.roleId,
          name: this.form.roleName,
          path: selectedNodes.join(","),
        });
        this.$message.success({
          message: res.msg,
        });
        this.form = {};
        this.close();
        this.getUserList();
      } catch (error) {
        console.log(error);
      }
    },
    close() {
      this.dialogVisible = false;
      this.modifyStatus = false;
      this.form = {};
      this.$refs.tree.setCheckedKeys([]);
    },
    // 翻页
    changePage(page) {
      this.pages = page;
      this.getUserList();
    },
    // 下一页
    next() {
      this.pages++;
      this.getUserList();
    },
  },
  components: {},
};
</script>

<style lang="scss" scoped>
.roleContent {
  display: flex;
  flex-direction: column;
  .addRole {
    align-self: flex-end;
    border: 1px solid #f7961c;
    padding: 7px 20px;
    border-radius: 4px;
    margin: 15px 0;
    cursor: pointer;
  }
  .sort-content {
    height: 900px;
    margin-top: 10px;
    .pagination {
      margin-top: 15px;
    }
  }
}
</style>
